@import "../../assets/css/Components.scss";
.portfolio-box{
    &.grid-style {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(25%, 1fr));
        grid-auto-rows: 200px;
        grid-auto-flow: dense;
        .portfolio-box-item {
            position: relative;
            display: inline-block;
            width: 100%;
            vertical-align: middle;
            overflow: hidden;
            opacity: 1;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
            &.box-square {
                grid-column: span 1;
                grid-row: span 2;
            }
            &.box-tall {
                grid-row: span 3;
            }
            &.box-square {
                grid-column: span 1;
                grid-row: span 2;
            }
        }
    }
    .video{
        width:100%;
        height:100%;
        object-fit: cover;
    }
}