@import "../../assets//css//Components.scss";
.section-service{
    @extend .d-flex;
    justify-content: space-between;
    width:100%;
        @media(max-width:1000px){
            flex-wrap: wrap;
        }
    .list-item{
        width:25%;
        height:550px;
        @media(max-width:1300px){
            height:450px;
        }
        @media(max-width:1000px){
            width:50%;
        }
        @media(max-width:375px){
            width:100%;
        }
        background-color: $gray-400;
        position: relative;
        &:nth-child(2){
            background-color: $gray-500;
        }
        &:nth-child(3){
            background-color: $gray-600;
        }
        &:nth-child(4){
            background-color: $gray-700;
        }
        img{
            width:100%;
            height:100%;
            object-fit: cover;
        }
        .heading-top{
            position:absolute;
            top:55px;
            left:55px;
            z-index: 2;
            .service-title{
                color:$white;
                text-transform: uppercase;
                font-size:.85rem;
                letter-spacing: 1.25px;
                margin-bottom:.4rem;
                transition: color .3s ease-in-out;
            }
            h4{
                color:$white;
                text-transform: uppercase;
                font-size:2rem;
                letter-spacing: 1.25px;
                margin-bottom:.4rem;
                transition: color .3s ease-in-out;
                span{
                    display: block;
                }
            }
        }
        .read-more{
            position:absolute;
            bottom:55px;
            left:55px;
            z-index: 2;
            color:$white;
            text-transform: uppercase;
            font-size:.85rem;
            letter-spacing: 1.25px;
            font-weight: 600;
            transition: color .3s ease-in-out;
        }
        &:before{
            position: absolute;
            width: 0; 
            height: 100%; 
            background-color: rgba($white, .7);
            left: 0;
            top: 0;
            content: "";
            z-index: 1;
            transition: width 0.3s ease-in-out;
        }
        &:hover{
            cursor: pointer;
            &:before{
                width: 100%;
            }
            .heading-top{
                .service-title{
                    color:$black;
                }
                h4{
                    color:$black;
                }
            }
            .read-more{
                color:$black;
            }
        }
    }
}