@import "../../assets//css/Components.scss";
.homeVideo{
    width: 100%;
    height: calc(100vh - 100px);
    position: relative;
    .video-wrap {
        width: 100%;
        height: inherit;
        .video{
            width: 100%;
            height: inherit;
            object-fit: cover;
        }
    }
    .video-content{
        position:absolute;
        top:50%;
        left:50%;
        transform: translate(-50%, -50%);
        width: 100%;
        @extend .d-flex;
        flex-direction: column;
        z-index: 2;      
        color: $white;  
        h1{
            font-size: 4rem;
            letter-spacing: 2;
            text-shadow: 1px 1px 0 #000, 2px 2px 0 #444, 3px 3px 0 #888;
        }
        h2{
            font-size: 2rem;
            letter-spacing: 2;
            text-shadow: 1px 1px 0 #000, 2px 2px 0 #444, 3px 3px 0 #888;
        }
    }
}